import React, { Component } from "react";
import background from "../../Images/bg-3.jpg";
import { SLink } from "./Elements";

export class Header extends Component {
  render() {
    return (
      <>
        <div
          className="container-fluid "
          style={{
            backgroundImage: `url(${background})`,
            backgroundPosition: ``,
            backgroundRepeat: `no-repeat`,
            backgroundSize: `100% `,
            height: `300px`,
            cursor: `context-menu`,
          }}
        >
          <div
            class="d-flex justify-content-between bg-transparent  text-white p-5"
            style={{ zIndex: `10` }}
          >
            <div>
              <h3>
                {" "}
                <span class="badge bg-success ">Pro</span>
              </h3>
              <span className="display-1">Lilly Sabri </span>{" "}
              <p className="m-1 ">bio.fm/lillysabri</p>
              <div className="mt-3 fs-6 lead ">
                <SLink href="http://mailchi.mp/leanwithlilly/freeworkouttimetable" target="_blank">
                  SignUp for free guides!
                </SLink>
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Header;
